___CSS_LOADER_EXPORT___.push([module.id, `body {
    margin: 0;
    padding: 0;
}

#todo-form-container {
    display: flex;
    justify-content: space-evenly;
}

#todo-form-container form, #new-project {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

#todo-form-container input {
    width: 350px;
    height: 35px;
    border: 1px solid rgba(0, 0, 0, 0.4);
    border-radius: 5px;
    font-size: 20px;
    padding: 0 10px;
}

#todo-form-container select {
    width: 350px;
    font-size: 18px;
    padding: 7px 10px;
}

#todo-form-container button {
    font-size: 18px;
    border: none;
    padding: 0 10px;
    background-color: cornflowerblue;
    color: #fff;
    border-radius: 5px;
    padding: 7px 10px;
}

#todo-form-container label {
    font-size:  20px;
}

#projects-container {
    padding: 15px 20px;
}

.project {
    margin: 30px 0;
}

.todos-container {
    padding: 15px 20px;
    display: grid;
    grid-template-columns: repeat(auto-fit, 400px);
    gap: 20px;
}

.todo {
    border: 1px solid rgba(0, 0, 0, 0.4);
    border-radius: 8px;
    padding: 0 15px 30px;
}

.expandible {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    gap: 10px;
    margin: 10px 0;
}

.hide {
    display: none;
}

.todo button {
    width: 100%;
}

.toggle-content {
    margin-top: 10px;
    color: blue;
    background-color: #fff;
    border: none;
    text-align: end;
    cursor: pointer;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,aAAa;IACb,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,oCAAoC;IACpC,kBAAkB;IAClB,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,YAAY;IACZ,eAAe;IACf,gCAAgC;IAChC,WAAW;IACX,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,8CAA8C;IAC9C,SAAS;AACb;;AAEA;IACI,oCAAoC;IACpC,kBAAkB;IAClB,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,gBAAgB;IAChB,SAAS;IACT,cAAc;AAClB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,sBAAsB;IACtB,YAAY;IACZ,eAAe;IACf,eAAe;AACnB","sourcesContent":["body {\n    margin: 0;\n    padding: 0;\n}\n\n#todo-form-container {\n    display: flex;\n    justify-content: space-evenly;\n}\n\n#todo-form-container form, #new-project {\n    display: flex;\n    flex-direction: column;\n    gap: 10px;\n}\n\n#todo-form-container input {\n    width: 350px;\n    height: 35px;\n    border: 1px solid rgba(0, 0, 0, 0.4);\n    border-radius: 5px;\n    font-size: 20px;\n    padding: 0 10px;\n}\n\n#todo-form-container select {\n    width: 350px;\n    font-size: 18px;\n    padding: 7px 10px;\n}\n\n#todo-form-container button {\n    font-size: 18px;\n    border: none;\n    padding: 0 10px;\n    background-color: cornflowerblue;\n    color: #fff;\n    border-radius: 5px;\n    padding: 7px 10px;\n}\n\n#todo-form-container label {\n    font-size:  20px;\n}\n\n#projects-container {\n    padding: 15px 20px;\n}\n\n.project {\n    margin: 30px 0;\n}\n\n.todos-container {\n    padding: 15px 20px;\n    display: grid;\n    grid-template-columns: repeat(auto-fit, 400px);\n    gap: 20px;\n}\n\n.todo {\n    border: 1px solid rgba(0, 0, 0, 0.4);\n    border-radius: 8px;\n    padding: 0 15px 30px;\n}\n\n.expandible {\n    display: flex;\n    flex-direction: column;\n    max-width: